.nc_settings {
  padding: 2rem 1.5rem;
  .nc_card {
    padding: 0 1.25rem;
    background-color: #FFFFFF;
    border-radius: .5rem;
    max-width: 1200px;
    margin: 0 auto;
    &+.nc_card {
      margin-top: 1.25rem;
    }
    .nc_table {
      width: 100%;
      border-top: 1px solid #EFEFEF;
      tr {
        height: 3.75rem;
      }
      th {
        text-align: left;
      }
    }
    .nc_line {
      height: 3.75rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
      &+.nc_line {
        border-top: 1px solid #EFEFEF;
      }
      .ncl_lt {
        display: flex;
        flex-direction: column;
        .ncl_lt_title {
          font-size: .875rem;
          color: #333333;
        }
        .ncl_lt_des {
          font-size: .75rem;
          color: #666666;
        }
      }
      .ncl_it {
        display: flex;
        input {
          width: 4.5rem;
        }
        .nc_slider {
          width: 6.5rem;
          margin-left: 1rem;
        }
      }
    }
  }
}

.chat_setting_modal {
  .cs_model_title {
    margin: 1rem 0 .625rem;
    color: #333333;
  }
  .ant-select {
    width: 100%;
  }
  .ant-radio-group {
    display: flex;
    align-items: center;
    input {
      width: 6rem;
    }
  }
}